<template>
  <v-app-bar
    id="app-bar"
    app
    dark
    short
    flat
  >
    <v-btn
      class="mr-3"
      elevation="1"
      fab
      small
      @click="$vuetify.breakpoint.smAndDown ? setDrawer(!drawer) : $emit('input', !value)"
    >
      <v-icon v-if="value">
        mdi-view-quilt
      </v-icon>

      <v-icon v-else>
        mdi-dots-vertical
      </v-icon>
    </v-btn>

    <v-toolbar-title
      class="hidden-sm-and-down font-weight-light"
      v-text="$t($route.name)"
    />

    <v-spacer/>

    <v-menu
      bottom
      left
      min-width="200"
      offset-y
      origin="top right"
      transition="scale-transition"
    >
      <template v-slot:activator="{ attrs, on }">
        <v-btn
          class="ml-2"
          min-width="0"
          text
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>mdi-account</v-icon>
        </v-btn>
      </template>

      <v-list
        :tile="false"
        flat
        nav
      >
        <app-bar-item>
          <v-list-item-title v-text="'Aktuelle Ausrüstung:'+$store.state.currentDeviceId"/>
        </app-bar-item>
        <template v-for="(p, i) in profile">
          <app-bar-item
            :key="`item-${i}`"
            to="/pages/login"
          >
            <v-list-item-title v-text="p.title"/>
          </app-bar-item>
        </template>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
  // Components
  import { VHover, VListItem } from 'vuetify/lib'
  // Utilities
  import { mapMutations, mapState } from 'vuex'

  const config = require('@/assets/config.json')
  export default {
    name: 'DashboardCoreAppBar',

    components: {
      AppBarItem: {
        render (h) {
          return h(VHover, {
            scopedSlots: {
              default: ({ hover }) => {
                return h(VListItem, {
                  attrs: this.$attrs,
                  class: {
                    'black--text': !hover,
                    'white--text secondary elevation-12': hover,
                  },
                  props: {
                    activeClass: '',
                    dark: hover,
                    link: true,
                    ...this.$attrs,
                  },
                }, this.$slots.default)
              },
            },
          })
        },
      },
    },

    props: {
      value: {
        type: Boolean,
        default: false,
      },
    },

    data: function () {
      return ({
        notifications: [
          this.$i18n.t('zh'),
          this.$i18n.t('en'),
          this.$i18n.t('de'),
        ],
        profile: [
          { title: 'Log out' },
        ],
      })
    },
    computed: {
      ...mapState(['drawer']),
    },

    methods: {
      changeLang (i) {
        console.log(this.$i18n.locale)
        this.$i18n.locale = config.supportLang[i].lang
        localStorage.setItem('lang', this.$i18n.locale)
        this.$router.go(0)
      },
      ...mapMutations({
        setDrawer: 'SET_DRAWER',
      }),
    },
  }
</script>
